import { FunctionComponent, useMemo, type CSSProperties } from "react";

type PilldefaultType = {
  beds?: string;

  /** Style props */
  pilldefaultBackgroundColor?: CSSProperties["backgroundColor"];
  pilldefaultFlexShrink?: CSSProperties["flexShrink"];
  bedsColor?: CSSProperties["color"];
};

const Pilldefault: FunctionComponent<PilldefaultType> = ({
  beds,
  pilldefaultBackgroundColor,
  pilldefaultFlexShrink,
  bedsColor,
}) => {
  const pilldefaultStyle: CSSProperties = useMemo(() => {
    return {
      backgroundColor: pilldefaultBackgroundColor,
      flexShrink: pilldefaultFlexShrink,
    };
  }, [pilldefaultBackgroundColor, pilldefaultFlexShrink]);

  const bedsStyle: CSSProperties = useMemo(() => {
    return {
      color: bedsColor,
    };
  }, [bedsColor]);

  return (
    <div
      className="rounded-8xl flex flex-row items-center justify-center py-[11px] px-[21px] text-center text-base text-primary-contrast font-components-button-large border-[1px] border-solid border-primary-contrast"
      style={pilldefaultStyle}
    >
      <div
        className="relative tracking-[0.04em] capitalize font-medium"
        style={bedsStyle}
      >
        {beds}
      </div>
    </div>
  );
};

export default Pilldefault;
