import { FunctionComponent, useState, useCallback } from "react";
import MatterhornPopup from "./MatterhornPopup";
import PortalPopup from "./PortalPopup";

type PopularStaysContainerType = {
  imageDimensions?: string;
  accommodationType?: string;
  accommodationImageUrl?: string;
  pricePerNight?: string;
  rating?: string;
  reviewCount?: string;
};

const PopularStaysContainer: FunctionComponent<PopularStaysContainerType> = ({
  imageDimensions,
  accommodationType,
  accommodationImageUrl,
  pricePerNight,
  rating,
  reviewCount,
}) => {
  const [isMatterhornPopupOpen, setMatterhornPopupOpen] = useState(false);

  const openMatterhornPopup = useCallback(() => {
    setMatterhornPopupOpen(true);
  }, []);

  const closeMatterhornPopup = useCallback(() => {
    setMatterhornPopupOpen(false);
  }, []);

  return (
    <>
      <div className="self-stretch flex-1 shrink-0 flex flex-row items-start justify-center py-0 px-3 box-border w-3/12 text-left text-base text-darkslategray-300 font-components-button-large md:w-[33.33%] sm:w-full sm:flex-[unset] sm:self-stretch">
        <div className="self-stretch flex-1 rounded-3xs bg-primary-contrast box-border flex flex-col items-start justify-start py-[15px] px-[13px] gap-[23px] w-3/12 border-[1px] border-solid border-whitesmoke-400 hover:bg-gainsboro-300 hover:cursor-pointer lg:w-[25%!important] md:w-full sm:w-[100%!important] sm:mb-5">
          <img
            className="self-stretch relative rounded-3xs max-w-full overflow-hidden h-[200px] shrink-0 object-cover"
            alt=""
            src={imageDimensions}
          />
          <div className="self-stretch flex flex-row items-start justify-start gap-[11px]">
            <div className="flex-1 flex flex-col items-start justify-start gap-[6px]">
              <div className="self-stretch relative tracking-[0.02em] text-gray-100">
                {accommodationType}
              </div>
              <b className="self-stretch relative text-3xl tracking-[0.04em] capitalize sm:text-xl">
                {accommodationImageUrl}
              </b>
              <div className="self-stretch relative text-lg tracking-[0.04em]">
                {pricePerNight}
              </div>
            </div>
            <img
              className="relative w-[25px] h-[25px] overflow-hidden shrink-0 object-cover cursor-pointer"
              alt=""
              src="/video1@2x.png"
              onClick={openMatterhornPopup}
            />
          </div>
          <div className="w-[270px] h-[19px] flex flex-row items-center justify-start gap-[9px]">
            <div className="w-[44.3px] h-[19px] flex flex-row items-center justify-start gap-[3px]">
              <img
                className="relative w-[16.6px] h-[17.2px] object-cover"
                alt=""
                src="/vector2@2x.png"
              />
              <div className="relative tracking-[0.04em] font-medium inline-block w-6 shrink-0">
                {rating}
              </div>
            </div>
            <div className="relative tracking-[0.04em] text-cornflowerblue-100 inline-block w-[216.4px] shrink-0">
              {reviewCount}
            </div>
          </div>
          <button className="cursor-pointer p-0 bg-[transparent] self-stretch rounded box-border h-[46px] overflow-hidden shrink-0 flex flex-col items-center justify-center border-[1px] border-solid border-lavender-200 sm:w-[100%!important]">
            <div className="relative text-mini tracking-[0.46px] leading-[26px] uppercase font-medium font-components-button-large text-cornflowerblue-100 text-left">
              More details
            </div>
          </button>
        </div>
      </div>
      {isMatterhornPopupOpen && (
        <PortalPopup
          overlayColor="rgba(0, 0, 0, 0.3)"
          placement="Centered"
          onOutsideClick={closeMatterhornPopup}
        >
          <MatterhornPopup onClose={closeMatterhornPopup} />
        </PortalPopup>
      )}
    </>
  );
};

export default PopularStaysContainer;
